import { makeStyles } from "@material-ui/core/styles";

const iconStar =
    "url('data:image/svg+xml;base64,')";

const useStyles = makeStyles(theme => ({
    wrap: {
        padding: ".625em 5px",
        textAlign: "center"
    },
    root: {
        display: "inline-flex",
        marginBottom: ".875em",
        paddingLeft: "2.75em",
        paddingRight: "2.75em",
        minHeight: "2.5em",
        fontSize: "1rem",
        background: `${iconStar} 0 50% no-repeat, ${iconStar} 100% 50% no-repeat`,
        backgroundSize: "2.5em"
    },
    info: {
        "& p": {
            margin: 0
        }
    },
    average: {
        fontSize: "1.75rem",
        lineHeight: 1,
        fontWeight: 700,
        color: "#3a5"
    },
    count: {
        fontSize: ".75rem",
        lineHeight: 1
    },
    divider: {
        marginLeft: "auto",
        marginRight: "auto",
        maxWidth: "10rem"
    }
}));

export default useStyles;
